export default {
    data: () => ({
        loadingopts:{ loader: 'dots', height:128, width:128, isFullPage:true, canCancel: true, 
                      color:'#2982DF', backgroundColor:'#000000', opacity:.5 }
    }),
    methods: {
        isMobile(){
            return window.matchMedia("only screen and (max-width: 768px)").matches;
        },
        showLoader(){
            let loader = this.$loading.show(this.loadingopts);
            return loader;
        },
        hideLoader(loader){
            loader.hide();
        }        
    }
}